
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import exchangeList from './exchangeList'
import AppCode from '@/components/common/Appcode'
import { guid } from '@/storage'
import { getQryInfo, getExchangeChildList, getExchangeCode } from 'api'
import Clipboard from 'clipboard'
export default {
data () {
return {
params: this.$route.query,
remain: '',
tabIndex: 0,
stickyTop: guid.get() ? 0 : 46,
info: '',
list: [],
page: 1,
noData: true,
loading: false
}
},
created () {
this.getQryInfo()
this.getList()
this.getExchangeCode()
},
mounted () {
var clipboard = new Clipboard('.copy')
let _this = this
clipboard.on('success', function (e) {
_this.$vux.toast.text('å¤å¶æå')
e.clearSelection()
})
clipboard.on('error', function (e) {
_this.$vux.toast.text('å¤å¶å¤±è´¥ï¼è¯·æå¨é¿æå¤å¶')
})
},
methods: {
toThousands (num) {
let integer = num.split('.')[0] || '0'
let decimal = num.split('.')[1]
decimal = (decimal && `.${decimal}`) || ''
let value = integer.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,') + decimal
return value
},
getQryInfo () {
let args = {
cmd: 'qryBal',
cc: this.params.token,
args: this.params.exchangeAcc
}
getQryInfo(args).then(res => {
let remain = res.data[this.params.token]
this.remain = this.toThousands(remain)
})
},
getExchangeCode () {
let args = {
exchangeAcc: this.params.exchangeAcc,
exchangeToken: this.params.token
}
getExchangeCode(args).then(res => {
this.info = res.data
})
},
getList () {
let args = {
page: this.page,
exchangeAddr: this.params.exchangeAcc
}
getExchangeChildList(args).then(res => {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
})
.finally(() => {
this.loading = false
})
},
loadMore () {
this.noData = true
this.page += 1
this.getList()
},
changeItem (val) {
this.tabIndex = val
}
},
components: {
XHeader,
Tab,
TabItem,
exchangeList,
Sticky,
AppCode,
LoadMore
}
}



